html {
  height: 100%;
  width: 100%;
}

body {
  background: none transparent;
  overflow: hidden;
  margin: 0;
  padding: 0;
  height: inherit;
  width: inherit;
}

#root {
  height: inherit;
  width: inherit;
}

.Annotation-Div {
  overflow: hidden;
}
.App {
  text-align: left;
  height: inherit;
  width: inherit;
}

.annotation-pass-through {
  pointer-events: none;
  background: none transparent;
}

.annotation-text-anchor-start {
  text-anchor: start;
}

.annotation-text-anchor-middle {
  text-anchor: middle;
}

.annotation-text-anchor-end {
  text-anchor: end;
}

.annotation-dash-0 {
  stroke-dasharray: 0;
}

.annotation-dash-1 {
  stroke-dasharray: 1;
}

.annotation-dash-2 {
  stroke-dasharray: 2;
}

.annotation-dash-3 {
  stroke-dasharray: 3;
}

.annotation-dash-4 {
  stroke-dasharray: 4;
}

.annotation-dash-5 {
  stroke-dasharray: 5;
}

.annotation-dash-6 {
  stroke-dasharray: 6;
}

.annotation-dash-7 {
  stroke-dasharray: 7;
}

.annotation-dash-8 {
  stroke-dasharray: 8;
}

.annotation-dash-9 {
  stroke-dasharray: 9;
}

.annotation-dash-10 {
  stroke-dasharray: 10;
}

.annotation-stroke-0-25 { 
  stroke-width: 0.25;
}

.annotation-stroke-0-5 { 
  stroke-width: 0.5;
}

.annotation-stroke-0-75 { 
  stroke-width: 0.75;
}

.annotation-stroke-1 { 
  stroke-width: 1;
}

.annotation-stroke-1-5 { 
  stroke-width: 1.5;
}

.annotation-stroke-2 { 
  stroke-width: 2;
}

.annotation-stroke-2-5 { 
  stroke-width: 2.5;
}

.annotation-stroke-3 { 
  stroke-width: 3;
}

.annotation-stroke-4 { 
  stroke-width: 4;
}

.annotation-stroke-5 { 
  stroke-width: 5;
}

#splashScreen img {
  max-height: 30px;
}

#splashScreen button.cta {
  max-width: 230px;
  margin-left: auto;
  margin-right: auto;
}

#splashScreen .info {
  font-style: italic;
  font-size: 11px;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --font-color: #668389;
  --accent-color: #9610ff;
}
html {
  font-size: 14px;
}

hr {
  border-top: 1px solid #d8d8d8;
  border-bottom: none;
}
